.container {
  max-width: 200px;

  &__card {
    border-radius: 5px;
    border: 1px solid getColor("card-border");
    padding: 32px 16px 16px;
  }
}

.card {
  transition: scale 300ms linear;

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 0;
    margin-top: 40px;

    font-size: 12px;
    font-weight: 500;
    line-height: 18px;

    transition: color 300ms linear;
  }

  &__product-code {
    margin-bottom: 0;
    margin-top: 4px;

    line-height: 14px;
    color: getColor("secondary-text");
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;

    &-count {
      margin: 0;
      line-height: 14px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0;
    margin-top: 24px;

    &-title,
    &-value {
      margin: 0;
    }

    &-title {
      font-size: 12px;
      line-height: 18px;
      color: getColor("secondary-text");
    }

    &-value {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__button-buy {
    display: block;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    line-height: 40px;
    margin-top: 16px;

    width: 100%;
    height: 40px;
    border-radius: 5px;
    background-color: getColor("accent-button");
    border: 1px solid transparent;
    transition-property: color, background-color, border-color;
    transition-duration: 300ms;
    transition-timing-function: linear;

    &:hover {
      color: getColor("accent-button");

      background-color: #fff;
      border-color: getColor("accent-button");
    }
  }

  &:hover {
    scale: 1.2;

    .card__title {
      color: getColor("accent-card-title");
    }
  }
}

.uppercase {
  text-transform: uppercase;
}
